body {
  background-color: #87b5b5;
}

code {
  background-color: white;
}
pre {
  background-color: white;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
  padding: 20px 0;
}

.editorComponentMin {
  background-color: #c0d8d8;
  width: 500px;
  min-height: 200px;
  border: 1px solid;
  padding: 0;
}
.editorComponentMax {
  background-color: #c0d8d8;
  width: 500px;
  border: 1px solid;
  padding: 0;
  min-height: 95vh;
  resize: none;
}

.previewComponentMax {
  background-color: #c0d8d8;
  width: 700px;
  min-height: 95vh;
  border: 1px solid;
}
.previewComponentMin {
  background-color: #c0d8d8;
  width: 700px;
  min-height: 570px;
  border: 1px solid;
}

.title {
  display: flex;
  height: 30px;
  background-color: #4aa3a3;
  border-bottom: 1px solid;
  align-items: center;
  padding: 5px;
  gap: 5px;
  font-weight: bold;
  font-family: monospace;
  font-size: 20px;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
}
.smileIcon {
  font-size: 16px;
}
.sizeButton {
  width: 25px;
  margin-left: auto;
  font-size: 16px;
}
.sizeButton:hover {
  cursor: pointer;
  color: aquamarine;
}

.editorMin {
  width: 100%;
  min-height: 170px;
  padding: 5px 0 0 5px;
  background: transparent;
  outline: none;
  border: none;
  margin: 0;
  font-size: 14x;
  font-family: monospace;
  margin-bottom: -5px;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
}
.editorMax {
  width: 100%;
  min-height: 95vh;
  resize: none;
  padding: 5px 0 0 5px;
  background: transparent;
  outline: none;
  border: none;
  margin: 0;
  font-size: 14x;
  font-family: monospace;
  margin-bottom: -5px;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
}

.previewMin {
  min-height: 570px;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
}
.previewMax {
  min-height: 95vh;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
}
#preview {
  padding: 10px 20px;
}

#preview img {
  display: block;
  width: 90%;
  margin: 2rem auto;
}

#preview h1 {
  border-bottom: 2px solid #224b4b;
}

#preview h2 {
  border-bottom: 2px solid #224b4b;
}

@media (max-width: 550px) {
  .editorComponentMin,
  .editorComponentMax {
    width: 90vw;
  }
}

@media (max-width: 720px) {
  .previewComponentMin, .previewComponentMax {
    width: 90vw;
  }
}
